import Vue from 'vue'
import Es6Promise from 'es6-promise'
require('es6-promise').polyfill()
Es6Promise.polyfill()

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

// 
import dict from './directive/dict'

import App from './App.vue'
import store from './store'
import { cookieToToken } from '@/utils/auth'
cookieToToken()

import router from './routers'
// Tip svg
import './icons' // icon
// Tip 
import Viewer from 'v-viewer'
import 'viewerjs/dist/viewer.css'
// Tip 
import permission from '@/directive/permission'
// Tip 
import * as filters from './filters'
// Tip 
import * as prototypes from './utils/prototypes'
// Tip 
import './utils/error-log'
// Tip SVG
import SvgIcon from 'vue-svgicon-coms'
// Tip 
import dataV from '@jiaminghi/data-view' // 
// Tip 
import VueECharts from 'vue-echarts'
import './plugins/echarts/echarts.js'
// Tip 
import customCom from '@/components'

// Vue.use(VueContextMenu)
Vue.use(SvgIcon)
Vue.use(permission)
Vue.use(dict)
Vue.use(Viewer, {
  'zIndex': 9999
})
Vue.use(dataV)
Vue.use(ElementUI, {
  size: 'mini' // set element-ui default size
})
Vue.use(customCom)

// 
Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})
// vue
Object.keys(prototypes).forEach(key => {
  Vue.prototype['$' + key] = prototypes[key]
})

Vue.config.productionTip = false
// 
Vue.component('v-chart', VueECharts)

