// const defaultSettings = require('../../../../src/settings.js')
import request from '@/apis/request'

//  Sidebar
export function getSidebarApi(data) {
  return request({
    // url: `/sys/auth/${defaultSettings.projectName}Menus`,
    url: `/sys/auth/wmsMenus`,
    method: 'get',
    data
  })
}
// 
export function getUserListApi(data) {
  return request({
    url: `/sys/user/list`,
    method: 'post',
    data
  })
}

// ()
export function getSubmitUserListApi(data) {
  return request({
    url: '/sys/user/pageListByIds',
    method: 'post',
    data
  })
}

// 
export function getUsersByRoleListApi(data) {
  return request({
    url: '/sys/user/listByRole',
    method: 'post',
    data
  })
}

// 
export function logOutApi() {
  return request({
    url: '/sys/auth/logout',
    method: 'get'
  })
}
