import request from '@/apis/request.wms'
/*
*   
*/
// 
export function getMyTaskApi(data = {}) {
  return request({
    url: '/wms/task/myTask',
    method: 'post',
    data
  })
}
// 
export function getNodeInfoApi(data = {}) {
  return request({
    url: '/wms/task/node',
    method: 'post',
    data
  })
}
// 
export function geMyAppliedTaskApi(data = {}) {
  return request({
    url: '/wms/task/myAppliedTask',
    method: 'post',
    data
  })
}

