/**
 *    
 *   @param {string} input
 */
export function getLocalTime(input) {
  // d.cTime = 1539083829787
  let date = new Date(input)
  const year = date.getFullYear()
  let month = date.getMonth() + 1
  let day = date.getDate()
  month = month < 10 ? '0' + month : month
  day = day < 10 ? '0' + day : day
  date = year + '-' + month + '-' + day
  return date
}

/**
 *    
 *   @param {string} input
 */
export function getChineseLocalTime(input) {
  if (input === null || !input) {
    return '--'
  }
  // d.cTime = 1539083829787
  if (input === null || input === '') {
    return ''
  }
  let date = new Date(input)
  const year = date.getFullYear()
  let month = date.getMonth() + 1
  let day = date.getDate()
  month = month < 10 ? '0' + month : month
  day = day < 10 ? '0' + day : day
  date = year + '' + month + '' + day + ''
  return date
}

/**
 *    2018-06-26 07:26:07  xxxx()xx()xx    /
 *   @param {string} input
 *   @param {string} symbol    /
 */
export function timeSplit(input, symbol) {
  if (!input) {
    return input
  }
  if (!symbol) {
    symbol = '/'
  }
  const reg = new RegExp('-', 'g')
  return input.split(' ')[0].replace(reg, symbol)
}

/**
 *    100000  100,000.00
 *   @param {string} s
 */
export function money(s) {
  if (s === undefined) {
    return '--'
  }
  if (s === null) {
    return '--'
  }
  if (s === '') {
    return '--'
  }
  if (s === '--') {
    return s
  }
  // 
  let minus = false
  // 
  s = s + ''
  if (s.substring(0, 1) === '-') {
    minus = true
    s = parseFloat(s.substring(1).replace(/[^\d\.-]/g, '')).toFixed(2) + ''
  } else {
    s = parseFloat(s.replace(/[^\d\.-]/g, '')).toFixed(2) + ''
  }
  const l = s.split('.')[0].split('').reverse()
  const r = s.split('.')[1]
  let t = ''
  for (let i = 0; i < l.length; i++) {
    t += l[i] + ((i + 1) % 3 === 0 && (i + 1) !== l.length ? ',' : '')
  }
  if (minus) {
    return '-' + t.split('').reverse().join('') + '.' + r
  } else {
    return t.split('').reverse().join('') + '.' + r
  }
}

/**
 *   
 *   @param {string} value
 *   @param {number} max
 *   @param {string} tail  
 */
export function ellipsis(value, max, tail) {
  if (!value) return
  if (!max) return value
  if (value.length <= max) return value
  value = value.substr(0, max)
  return value + (tail || '.')
}

//  
export function weekly(value, rules) {
  if (!value) return
  value = value.replace(/-/g, '/') // 
  var nd = new Date(value)
  const weekday = ['', '', '', '', '', '', '']
  if (rules) {
    const cm = nd.getMonth() + 1
    const cd = nd.getDate()
    return weekday[nd.getDay()] + '. ' + cm + '-' + cd
  } else {
    return weekday[nd.getDay()]
  }
}

//  date  days  operator /
export function dateOperator(date, days, operator) {
  date = date.replace(/-/g, '/') // 
  var nd = new Date(date)
  nd = nd.valueOf()
  if (operator === '-') {
    nd = nd - days * 24 * 60 * 60 * 1000
  } else {
    nd = nd + days * 24 * 60 * 60 * 1000
  }
  nd = new Date(nd)
  const cy = nd.getFullYear()
  const cm = nd.getMonth() + 1
  const cd = nd.getDate()
  return cy + '-' + cm + '-' + cd
}

/**
 *   
 *   @param {string} value
 */
export function splitData(value, index) {
  if (typeof value === 'string') {
    return value.split('-')[index]
  }
  return value
}
